"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { ChartContainer, ChartTooltip, ChartTooltipContent } from "@/components/ui/chart"
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, ResponsiveContainer } from "recharts"
import { 
  generateReport, 
  downloadReportAsJSON, 
  downloadReportAsCSV, 
  downloadReportAsPDF,
  type ReportData 
} from "@/lib/reportUtils"

export default function ReportsPage() {
  const [reportData, setReportData] = useState<ReportData | null>(null)
  const [error, setError] = useState("")
  const [isGenerating, setIsGenerating] = useState(false)

  const handleGenerateReport = async () => {
    try {
      setIsGenerating(true)
      setError("")
      
      // Simulate some processing time for better UX
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      const data = generateReport()
      setReportData(data)
    } catch (err) {
      setError("Failed to generate report. Please try again.")
      console.error("Report generation error:", err)
    } finally {
      setIsGenerating(false)
    }
  }

  const handleExport = (format: 'json' | 'csv' | 'pdf') => {
    if (!reportData) {
      setError("No report data available to export.")
      return
    }

    try {
      switch (format) {
        case 'json':
          downloadReportAsJSON(reportData)
          break
        case 'csv':
          downloadReportAsCSV(reportData)
          break
        case 'pdf':
          downloadReportAsPDF(reportData)
          break
      }
      setError("")
    } catch (err) {
      setError(`Error exporting report as ${format.toUpperCase()}.`)
      console.error("Export error:", err)
    }
  }

  const getChartData = () => {
    if (!reportData) return []
    
    return [
      {
        metric: "CO₂ Reduction",
        value: reportData.metrics.co2Savings,
        percentage: reportData.percentageImprovements.co2Reduction,
        unit: "kg"
      },
      {
        metric: "Water Savings",
        value: reportData.metrics.waterSavings,
        percentage: reportData.percentageImprovements.waterReduction,
        unit: "L"
      },
      {
        metric: "Energy Savings",
        value: reportData.metrics.energySavings,
        percentage: reportData.percentageImprovements.energyReduction,
        unit: "kWh"
      },
      {
        metric: "Waste Reduction",
        value: reportData.metrics.wasteReduction,
        percentage: reportData.percentageImprovements.wasteReduction,
        unit: "kg"
      }
    ]
  }

  return (
    <div className="space-y-8">
      <div className="space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">ESG Reports</h1>
        <p className="text-muted-foreground">
          Generate comprehensive environmental impact reports for ESG compliance and sustainability audits
        </p>
      </div>

      {/* Report Generation */}
      <Card>
        <CardHeader>
          <CardTitle>Generate New Report</CardTitle>
          <CardDescription>
            Create a comprehensive ESG impact assessment based on current material data
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
          
          <Button 
            onClick={handleGenerateReport}
            disabled={isGenerating}
            size="lg"
            className="w-full sm:w-auto"
          >
            {isGenerating ? "Generating Report..." : "Generate ESG Report"}
          </Button>
          
          {isGenerating && (
            <div className="flex items-center space-x-2 text-sm text-muted-foreground">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></div>
              <span>Analyzing material data and calculating environmental impact...</span>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Report Display */}
      {reportData && (
        <div className="space-y-6">
          {/* Report Header */}
          <Card>
            <CardHeader>
              <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-2 sm:space-y-0">
                <div>
                  <CardTitle className="text-2xl">{reportData.title}</CardTitle>
                  <CardDescription>
                    Generated on {new Date(reportData.generatedAt).toLocaleString()}
                  </CardDescription>
                </div>
                <Badge variant="secondary" className="w-fit">
                  Report ID: {reportData.id.split('_')[1]}
                </Badge>
              </div>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center p-4 bg-accent/30 rounded-lg">
                  <div className="text-2xl font-bold">{reportData.summary.totalMaterials}</div>
                  <div className="text-sm text-muted-foreground">Total Materials</div>
                </div>
                <div className="text-center p-4 bg-accent/30 rounded-lg">
                  <div className="text-2xl font-bold">{reportData.summary.traditionalMaterials}</div>
                  <div className="text-sm text-muted-foreground">Traditional Materials</div>
                </div>
                <div className="text-center p-4 bg-accent/30 rounded-lg">
                  <div className="text-2xl font-bold">{reportData.summary.smartMaterials}</div>
                  <div className="text-sm text-muted-foreground">Smart Materials</div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Environmental Impact Metrics */}
          <Card>
            <CardHeader>
              <CardTitle>Environmental Impact Summary</CardTitle>
              <CardDescription>
                Potential savings when transitioning from traditional to smart materials
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
                <div className="text-center p-4 bg-green-50 dark:bg-green-950/20 rounded-lg border border-green-200 dark:border-green-800">
                  <div className="text-2xl font-bold text-green-600">
                    {reportData.metrics.co2Savings.toLocaleString()} kg
                  </div>
                  <div className="text-sm text-muted-foreground">CO₂ Reduction</div>
                  <div className="text-xs text-green-600 font-medium">
                    {reportData.percentageImprovements.co2Reduction}% improvement
                  </div>
                </div>
                
                <div className="text-center p-4 bg-blue-50 dark:bg-blue-950/20 rounded-lg border border-blue-200 dark:border-blue-800">
                  <div className="text-2xl font-bold text-blue-600">
                    {reportData.metrics.waterSavings.toLocaleString()} L
                  </div>
                  <div className="text-sm text-muted-foreground">Water Savings</div>
                  <div className="text-xs text-blue-600 font-medium">
                    {reportData.percentageImprovements.waterReduction}% improvement
                  </div>
                </div>
                
                <div className="text-center p-4 bg-amber-50 dark:bg-amber-950/20 rounded-lg border border-amber-200 dark:border-amber-800">
                  <div className="text-2xl font-bold text-amber-600">
                    {reportData.metrics.energySavings.toLocaleString()} kWh
                  </div>
                  <div className="text-sm text-muted-foreground">Energy Savings</div>
                  <div className="text-xs text-amber-600 font-medium">
                    {reportData.percentageImprovements.energyReduction}% improvement
                  </div>
                </div>
                
                <div className="text-center p-4 bg-red-50 dark:bg-red-950/20 rounded-lg border border-red-200 dark:border-red-800">
                  <div className="text-2xl font-bold text-red-600">
                    {reportData.metrics.wasteReduction.toLocaleString()} kg
                  </div>
                  <div className="text-sm text-muted-foreground">Waste Reduction</div>
                  <div className="text-xs text-red-600 font-medium">
                    {reportData.percentageImprovements.wasteReduction}% improvement
                  </div>
                </div>
              </div>

              {/* Impact Chart */}
              <ChartContainer config={{}}>
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={getChartData()}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="metric" />
                    <YAxis />
                    <ChartTooltip content={<ChartTooltipContent />} />
                    <Bar dataKey="value" fill="#22c55e" />
                  </BarChart>
                </ResponsiveContainer>
              </ChartContainer>
            </CardContent>
          </Card>

          {/* Recommendations */}
          <Card>
            <CardHeader>
              <CardTitle>Strategic Recommendations</CardTitle>
              <CardDescription>
                Data-driven insights for sustainable material transition
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {reportData.recommendations.map((recommendation, index) => (
                  <div key={index} className="flex items-start space-x-3 p-3 bg-accent/30 rounded-lg">
                    <div className="flex-shrink-0 w-6 h-6 bg-primary text-primary-foreground rounded-full flex items-center justify-center text-sm font-medium">
                      {index + 1}
                    </div>
                    <p className="text-sm">{recommendation}</p>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Material Breakdown */}
          <Card>
            <CardHeader>
              <CardTitle>Material Analysis</CardTitle>
              <CardDescription>
                Detailed breakdown of traditional vs smart materials
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-6">
                {reportData.materialBreakdown.map((category, categoryIndex) => (
                  <div key={categoryIndex}>
                    <h4 className="font-semibold text-lg mb-3">{category.category}</h4>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                      {category.materials.map((material, materialIndex) => (
                        <div key={materialIndex} className="p-3 border rounded-lg space-y-2">
                          <h5 className="font-medium">{material.name}</h5>
                          <div className="grid grid-cols-2 gap-2 text-sm text-muted-foreground">
                            <div>CO₂: {material.co2} kg</div>
                            <div>Water: {material.water} L</div>
                            <div>Energy: {material.energy} kWh</div>
                            <div>Waste: {material.waste} kg</div>
                          </div>
                        </div>
                      ))}
                    </div>
                    {categoryIndex < reportData.materialBreakdown.length - 1 && (
                      <Separator className="mt-6" />
                    )}
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Export Options */}
          <Card>
            <CardHeader>
              <CardTitle>Export Report</CardTitle>
              <CardDescription>
                Download the report in various formats for sharing and compliance
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex flex-col sm:flex-row gap-3">
                <Button 
                  onClick={() => handleExport('json')}
                  variant="outline"
                  className="flex-1"
                >
                  Export as JSON
                </Button>
                <Button 
                  onClick={() => handleExport('csv')}
                  variant="outline"
                  className="flex-1"
                >
                  Export as CSV
                </Button>
                <Button 
                  onClick={() => handleExport('pdf')}
                  variant="outline"
                  className="flex-1"
                >
                  Export as Text Report
                </Button>
              </div>
              <p className="text-xs text-muted-foreground mt-2">
                Reports include all metrics, recommendations, and material analysis data
              </p>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  )
}
