import materialsData from "@/data/materials.json"

export interface ReportData {
  id: string
  title: string
  generatedAt: string
  summary: {
    totalMaterials: number
    traditionalMaterials: number
    smartMaterials: number
  }
  metrics: {
    co2Savings: number
    waterSavings: number
    energySavings: number
    wasteReduction: number
  }
  percentageImprovements: {
    co2Reduction: number
    waterReduction: number
    energyReduction: number
    wasteReduction: number
  }
  materialBreakdown: Array<{
    category: string
    materials: Array<{
      name: string
      co2: number
      water: number
      energy: number
      waste: number
    }>
  }>
  recommendations: string[]
}

export function generateReport(): ReportData {
  const traditionalMaterials = materialsData.filter(m => m.category === "Traditional")
  const smartMaterials = materialsData.filter(m => m.category === "Smart")
  
  // Calculate totals
  const totalTraditionalCO2 = traditionalMaterials.reduce((sum, m) => sum + m.co2, 0)
  const totalSmartCO2 = smartMaterials.reduce((sum, m) => sum + m.co2, 0)
  const co2Savings = totalTraditionalCO2 - totalSmartCO2
  
  const totalTraditionalWater = traditionalMaterials.reduce((sum, m) => sum + m.water, 0)
  const totalSmartWater = smartMaterials.reduce((sum, m) => sum + m.water, 0)
  const waterSavings = totalTraditionalWater - totalSmartWater
  
  const totalTraditionalEnergy = traditionalMaterials.reduce((sum, m) => sum + m.energy, 0)
  const totalSmartEnergy = smartMaterials.reduce((sum, m) => sum + m.energy, 0)
  const energySavings = totalTraditionalEnergy - totalSmartEnergy
  
  const totalTraditionalWaste = traditionalMaterials.reduce((sum, m) => sum + m.waste, 0)
  const totalSmartWaste = smartMaterials.reduce((sum, m) => sum + m.waste, 0)
  const wasteReduction = totalTraditionalWaste - totalSmartWaste

  // Calculate percentage improvements
  const co2Reduction = Math.round((co2Savings / totalTraditionalCO2) * 100)
  const waterReduction = Math.round((waterSavings / totalTraditionalWater) * 100)
  const energyReduction = Math.round((energySavings / totalTraditionalEnergy) * 100)
  const wasteReductionPercent = Math.round((wasteReduction / totalTraditionalWaste) * 100)

  // Generate recommendations
  const recommendations = [
    `Prioritize materials with highest CO₂ reduction potential (${co2Reduction}% average improvement)`,
    `Focus on water-intensive processes where smart materials show ${waterReduction}% water savings`,
    `Implement energy-efficient materials to achieve ${energyReduction}% energy reduction`,
    `Target waste reduction initiatives with ${wasteReductionPercent}% improvement potential`,
    "Establish baseline measurements for all material transitions",
    "Create phased implementation plan starting with highest-impact materials",
    "Monitor and track progress quarterly for continuous improvement"
  ]

  return {
    id: `report_${Date.now()}`,
    title: "GreenMat Pro ESG Impact Assessment",
    generatedAt: new Date().toISOString(),
    summary: {
      totalMaterials: materialsData.length,
      traditionalMaterials: traditionalMaterials.length,
      smartMaterials: smartMaterials.length
    },
    metrics: {
      co2Savings,
      waterSavings,
      energySavings,
      wasteReduction
    },
    percentageImprovements: {
      co2Reduction,
      waterReduction,
      energyReduction,
      wasteReduction: wasteReductionPercent
    },
    materialBreakdown: [
      {
        category: "Traditional Materials",
        materials: traditionalMaterials.map(m => ({
          name: m.name,
          co2: m.co2,
          water: m.water,
          energy: m.energy,
          waste: m.waste
        }))
      },
      {
        category: "Smart Materials",
        materials: smartMaterials.map(m => ({
          name: m.name,
          co2: m.co2,
          water: m.water,
          energy: m.energy,
          waste: m.waste
        }))
      }
    ],
    recommendations
  }
}

export function downloadReportAsJSON(data: ReportData) {
  const dataStr = JSON.stringify(data, null, 2)
  const blob = new Blob([dataStr], { type: "application/json" })
  const url = URL.createObjectURL(blob)
  const anchor = document.createElement("a")
  anchor.href = url
  anchor.download = `greenmat_esg_report_${new Date().toISOString().split('T')[0]}.json`
  anchor.click()
  URL.revokeObjectURL(url)
}

export function downloadReportAsCSV(data: ReportData) {
  // Create CSV content
  let csvContent = "GreenMat Pro ESG Impact Assessment\n"
  csvContent += `Generated: ${new Date(data.generatedAt).toLocaleString()}\n\n`
  
  // Summary section
  csvContent += "SUMMARY\n"
  csvContent += "Metric,Value\n"
  csvContent += `Total Materials,${data.summary.totalMaterials}\n`
  csvContent += `Traditional Materials,${data.summary.traditionalMaterials}\n`
  csvContent += `Smart Materials,${data.summary.smartMaterials}\n\n`
  
  // Environmental Impact section
  csvContent += "ENVIRONMENTAL IMPACT SAVINGS\n"
  csvContent += "Metric,Absolute Savings,Percentage Improvement\n"
  csvContent += `CO2 Emissions (kg),${data.metrics.co2Savings},${data.percentageImprovements.co2Reduction}%\n`
  csvContent += `Water Usage (L),${data.metrics.waterSavings},${data.percentageImprovements.waterReduction}%\n`
  csvContent += `Energy Consumption (kWh),${data.metrics.energySavings},${data.percentageImprovements.energyReduction}%\n`
  csvContent += `Waste Generation (kg),${data.metrics.wasteReduction},${data.percentageImprovements.wasteReduction}%\n\n`
  
  // Material breakdown
  data.materialBreakdown.forEach(category => {
    csvContent += `${category.category.toUpperCase()}\n`
    csvContent += "Material Name,CO2 (kg),Water (L),Energy (kWh),Waste (kg)\n"
    category.materials.forEach(material => {
      csvContent += `${material.name},${material.co2},${material.water},${material.energy},${material.waste}\n`
    })
    csvContent += "\n"
  })
  
  // Recommendations
  csvContent += "RECOMMENDATIONS\n"
  data.recommendations.forEach((rec, index) => {
    csvContent += `${index + 1},"${rec}"\n`
  })
  
  const blob = new Blob([csvContent], { type: "text/csv" })
  const url = URL.createObjectURL(blob)
  const anchor = document.createElement("a")
  anchor.href = url
  anchor.download = `greenmat_esg_report_${new Date().toISOString().split('T')[0]}.csv`
  anchor.click()
  URL.revokeObjectURL(url)
}

